import * as React from 'react';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import Course from './Course';
import { useState,useEffect } from 'react';
import Search from './search';
import { Divider } from '@mui/material';
import Match from './match';
let AllCourses=[];
const d = new Date();
let day = d.getDay()
export default function App() {

  const [courses, setCourses] = useState([]);
 
  useEffect(() => {
    fetch("https://us-west1-cooding-io.cloudfunctions.net/salas")
      .then((response) => response.json())
      .then((courses) => {
        AllCourses = courses.data.allSalasUdps.edges.filter(course=> course.node.day == day);
        setCourses(AllCourses);

      });
  }, []);

  let find = (value) => {
    if (value=="") {
      setCourses(AllCourses);
      return 
    }
    let courses = AllCourses.filter(course => Match(course,value));
    setCourses(courses);
  }

  return (
    <Container >
      <Search Find={find} />
      <Divider />

    { courses.map((course,ix)=> { 
      return  <Course key={ix}  course={course}/>  } )}

    </Container>
  );
}
